<script setup lang="ts">
import { useI18n } from "vue-i18n";
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  ITenantAttribute,
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesFileValueConfiguration } from "./models";

defineProps<{
  attribute: ITenantAttribute;
  dataTypeInstance: IDataTypeInstance<IDataTypesFileValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesFileValueConfiguration>({
  set: ({ minFiles, maxFiles, ...rest }) =>
    ({
      maxFiles: Number(maxFiles),
      minFiles: Number(minFiles),
      ...rest,
    }) as IDataTypesFileValueConfiguration,
});

const { t } = useI18n();
</script>

<template>
  <ACol>
    <ATextField
      v-model="configuration.prefixField"
      :label="t('datahub-plugin.files.prefixField')"
      :readonly="readonly"
      :hint="t('datahub-plugin.files.prefixHint')"
      persistent-hint
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.minFiles"
      :label="t('datahub-plugin.files.minFiles')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.maxFiles"
      :label="t('datahub-plugin.files.maxFiles')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model="configuration.supportedFileTypes"
      :label="t('datahub-plugin.files.supportedFileTypes')"
      :readonly="readonly"
      :hint="t('datahub-plugin.files.supportedFileTypesHint')"
      persistent-hint
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="t('datahub-plugin.stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>

